import React from "react";
import Joi from "joi-browser";
// import { NavLink } from "react-router-dom";
import Form from "../../base/form.jsx";
import request from "../../services/requestService.js";
import { toast } from "react-toastify";




class ImageColorForm extends Form {
    state = {
        data: { 
            image : null,
            color: "",
        },
        errors: {},
        buttonDisabled: false
    };

    schema = {
        image : Joi.any().label(""),
        color : Joi.string().label(""),
    };


    render() {
        return (
            <>
                <form onSubmit={this.handleSubmit} method="post">
                    <div class="box-field image-upload">
                        {this.renderInput("image", "", "file", "input")}
                        {this.renderInput("color", "", "color", "input color-input")}
                    </div>

                    {this.renderButton("   ", false, "btn btn-primary")}
                </form>
            </>
        );
    }
}

export default ImageColorForm;