import React from "react";
import Joi from "joi-browser";
import { NavLink } from "react-router-dom";
import Form from "../../base/form.jsx";


class LoginForm extends Form {
  state = {
    data: {
      username: "",
      password: "",
    },
    errors: {},
    buttonDisabled: false,
  };

  schema = {
    username: Joi.string()
      .required()
      .label(" "),
    password: Joi.string()
      .required()
      .label(" ")
  };


  render() {
    return (
      <form onSubmit={this.handleSubmit} method="post" className="form-small">
        <div className="segment">
          <h1>   </h1>
        </div>
        
        <label>
          {this.renderInput("username", "", "text", "input", {placeholder: " "})}
        </label>
        <label>
          {this.renderInput("password", "", "password", "input", {placeholder: " "})}
        </label>
        {this.renderButton("", this.buttonDisabled, "button red")}
        <p className="mt-3 text-center">     <NavLink to="/register"> </NavLink></p>
      </form>
    );
  }
}

export default LoginForm;
